@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;

}



.courses-container {
    padding-top: 120px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    height: auto;
    width: auto;
    border-radius: 10px;
    color: black;
    margin-bottom: -100px;
    position: relative;

}

.courses-container .courseicon {
    height: 60px;
    width: 60px;
    object-fit: cover;
    top: 90px;
    left: 422px;
    position: fixed;
    z-index: 1;
}

.courses-container .circle-round-course {
    height: 90px;
    width: 90px;
    object-fit: cover;
    top: 75px;
    left: 407px;
    position: fixed;
    z-index: 1;
    animation: rotateCircleRoundCourse 10s linear infinite;
}

@keyframes rotateCircleRoundCourse {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}


.courses-container .course-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 350px;
    height: auto;
    margin-bottom: 100px;
    padding: 10px;
    border-radius: 20px;
}

.courses-container .course-card:hover {
    box-shadow: 0px 0px 5px #4d2c5e;
    cursor: pointer;
}

.courses-container .course-card .course-card-img {
    position: relative;
    width: 350px;
    height: 350px;
    border-radius: 10px;
}

.courses-container .course-card .course-card-img .discount-tag {
    position: absolute;
    background: #fff;
    padding: 3px;
    border-radius: 5px;
    color: black;
    right: 10px;
    top: 10px;
    text-transform: capitalize;

}

.courses-container .course-card .course-card-img img {
    width: 350px;
    height: 350px;
    border-radius: 15px;
    object-fit: cover;
}




.courses-container .course-card .course-card-img .card-btn {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px;
    width: auto;
    text-transform: capitalize;
    border: none;
    outline: none;
    background-color: #ff7426;
    border-radius: 5px;
    transition: 0.2s;
    cursor: pointer;
    color: white;

}

.courses-container .course-card .course-card-img .card-btn:hover {
    background-color: #4d2c5e;
}


.courses-container .course-card .product-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-transform: uppercase;
    margin-top: 10px;

}


.courses-container .course-card .product-info .product-short-description {
    width: 100%;
    height: 20px;
    line-height: 20px;
    overflow: hidden;
    opacity: 0.7;
    text-transform: capitalize;
    margin: 5px 0;


}

.courses-container .course-card .product-info .price {
    font-weight: 500;
    font-size: 20px;


}

.courses-container .course-card .product-info .actual-price {
    opacity: 0.5;
    text-decoration: line-through;

}