@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;

}


.about-container {
    height: 75vh;
    width: 55vw;
    display: flex;
    align-items: center;
    margin-top: 120px;
    margin-right: auto;
    margin-left: auto;
    flex-direction: column;
    background-color: white;
    border: 5px solid #ff7426;
    border-radius: 50px;
    cursor: default;
    position: relative;


}

.about-container .abouthead {
    position: absolute;
    height: 35px;
    width: 35px;
    object-fit: cover;
    top: -32px;
    left: -31px;
}

.about-container .circleround-about {
    position: absolute;
    height: 80px;
    width: 80px;
    object-fit: cover;
    top: -57px;
    left: -54px;
    animation: rotateCircleRound 8s linear infinite;
}

@keyframes rotateCircleRound {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}


.about-container:hover {
    box-shadow: 0px 0px 10px #ff7426;
}

.about-container .top-about {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px;
}

.about-container .top-about .icon-text {
    display: flex;
    align-items: center;
}

.about-container .top-about .icon-text p {
    font-weight: 300;
    font-size: 0.9rem;
}

.about-container .top-about .icon-text .top-about-icon {
    width: 30px;
    height: 30px;
    object-fit: cover;
    margin-right: 10px;
}

.about-container .bottom-about {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px;

}

.about-container .bottom-about .bottom-about-image .main-about-img {
    height: 250px;
    width: 300px;
    object-fit: contain;
    margin-top: -30px;
}

.about-container .bottom-about .main-about-text {
    padding: 20px;
    margin-top: -30px;
    font-weight: 500;
    font-size: 1.2rem;

}


@media (max-width: 768px),
(max-width: 480px) {
    .about-container {
        height: auto;
        width: 85vw;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 120px;
        margin-right: auto;
        margin-left: auto;
        background-color: white;
        border: 5px solid #ff7426;
        border-radius: 50px;


    }

    .about-container:hover {
        box-shadow: 0px 0px 10px #ff7426;
    }

    .about-container .top-about {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        padding: 15px;
        margin-top: 15px;
        margin-bottom: 15px;
    }

    .about-container .top-about .icon-text {
        margin-bottom: 15px;

    }

    .about-container .bottom-about {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 15px;

    }
}