@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;

}





/* for main view */
.main {
    display: flex;

}

.left-body {
    flex: 2;
    margin-top: 50px;
    margin-bottom: 100px;
    margin-right: 10px;
    margin-left: 100px;

}

.left-body-text {
    padding: 20px 50px;
}

.left-body-text .body-heading {
    margin-bottom: 5px;
    margin-top: 100px;
    font-weight:
        800;
}

.left-body-text .body-para {
    font-weight: 300;
    font-size: 0.9rem;
}

.body-heading span {
    color: #FF7426;
}

.left-body-input {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-left: 50px;
    height: 40px;
    border-radius: 50px;
    padding: 0px 20px;
}

.left-body-input input {
    width: 200px;
    border: none;
    outline: none;
}

.left-body-input button {
    margin-left: 5px;
    font-size: 0.9rem;
    padding: 5px 18px;
    background-color: #4d2c5e;
    color: white;
    border: none;
    border-radius: 30px;
    cursor: pointer;
}


.left-body-input button:hover {
    box-shadow: 0px 0px 5px black;
}

.left-body-input button:active {
    background: linear-gradient(to right, #FF7426, #4d2c5e);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}

.right-body {
    flex: 3;
    margin-top: 50px;
    margin-bottom: 100px;
    margin-right: 100px;
    margin-left: 10px;
}

.main-body-img {
    height: 100%;
    width: 100%;
}


@media (max-width: 768px) {
    .left-body {
        margin: 75px auto;

    }

    .left-body-text {
        padding: auto
    }

    .right-body {
        display: none;
    }
}

@media (max-width: 480px) {

    .left-body {
        margin: 45px auto;

    }

    .left-body-text {
        padding: auto
    }

    .right-body {
        display: none;
    }
}














/* for overlay */
.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    top: 0;
    left: 0;
    z-index: -1;
}

.overlay .square {
    width: auto;
    opacity: 0.13;
    position: relative;
    top: 15px;
    left: 30px;
}

.overlay .circle {
    width: auto;
    opacity: 0.13;
    position: relative;
    top: 345px;
    left: 60px;
}

.overlay .half-circle1 {
    width: auto;
    opacity: 0.13;
    position: relative;
    top: 75px;
    left: 120px;
}

.overlay .xshape {
    width: auto;
    opacity: 0.13;
    position: relative;
    top: 105px;
    left: 150px;
}

.overlay .wave1 {
    width: auto;
    opacity: 0.13;
    position: relative;
    top: 145px;
    left: 80px;
}

.overlay .wave2 {
    width: auto;
    opacity: 0.13;
    position: relative;
    top: 185px;
    left: 110px;
}

.overlay .triangle {
    width: auto;
    opacity: 0.13;
    position: relative;
    top: 205px;
    left: 240px;
}

.overlay .points2 {
    width: auto;
    opacity: 0.13;
    position: relative;
    top: 245px;
    left: 90px;
}

















/* for infoDiv */

.info-container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 70vw;
    height: auto;
    margin: 50px auto;
    background-color: #4d2c5e;
    border-radius: 15px;
}

.info-item1,
.info-item2,
.info-item3 {
    display: flex;
    padding: 50px 20px;
    align-items: center;
}


.info-item2 .icon img,
.info-item3 .icon img,
.info-item1 .icon img {
    width: 30px;
    height: 30px;
    object-fit: cover;
    margin-right: 15px;
    border: 12px solid #ffffff59;
    border-radius: 15px;
}

.info-item2 .text h4,
.info-item3 .text h4,
.info-item1 .text h4 {
    color: white;
    font-weight: 600;
    margin-bottom: 5px;
}

.info-item2 .text p,
.info-item3 .text p,
.info-item1 .text p {
    color: white;
    font-weight: 200;
    font-size: 0.8rem;

}

@media (max-width: 768px) {
    .info-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 50px auto;
    }

    .info-item1,
    .info-item2,
    .info-item3 {
        display: flex;
        padding: 20px 20px;
        align-items: center;
    }

}

@media (max-width: 480px) {
    .info-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 90vw;
        margin: 25px auto;

    }

    .info-item1,
    .info-item2,
    .info-item3 {
        display: flex;
        padding: 50px 20px;
        align-items: center;
    }


}










/* for courses */
.courses {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 90vw;
    margin: 10px auto;
    flex-direction: column;

}

.courses .course-div-head {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.courses .course-cards {
    display: flex;
    margin-top: 40px;
    position: relative;
}

.courses .course-cards .ideaimg {
    position: absolute;
    top: -45px;
    left: -45px;
    height: 62px;
    width: 62px;
    transform: rotate(-45deg);
    object-fit: cover;
}

.courses .course-cards .curlyarrow {
    position: absolute;
    top: -80px;
    right: -95px;
    height: 92px;
    width: 92px;
    transform: rotate(-160deg);
    object-fit: cover;
}


.courses .course-cards .card {
    border-radius: 15px;
    margin: 0 5px;
    transition: transform 0.3s;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;


}

.courses .course-cards .card:hover {
    box-shadow: 0px 0px 14px 0px rgba(0, 0, 0, 0.35);
    -webkit-box-shadow: 0px 0px 14px 0px rgba(0, 0, 0, 0.35);
    -moz-box-shadow: 0px 0px 14px 0px rgba(0, 0, 0, 0.35);
    transform: scale(0.98);
}

.courses .course-cards .card img {
    height: 180px;
    width: 320px;
    object-fit: cover;
    padding: 5px;
    border-radius: 15px
}


.courses .course-cards .card .info .name-rating {
    display: flex;
    align-items: center;
    justify-content: space-between;


}

.courses .course-cards .card .info .name-rating .rating-container .ratingstar {
    width: 12px;
    height: 12px;
    object-fit: cover;
    margin: 0;
    padding: 0;
}

.courses .course-cards .card .info .name-rating p {
    opacity: 0.7;
    font-size: 0.8rem;
    font-weight: 300;
}


.courses .course-cards .card .info .price {

    color: #FF7426;
}

.courses .course-cards .card .info hr {
    width: 100%;
    border: none;
    border-top: 1px solid #4745455e;
    margin: 4px 0;

}

.courses .course-cards .card .info .card-bottom-icons {
    display: flex;
    align-items: center;
    margin: 0 3px;
}

.courses .course-cards .card .info .card-bottom-icons .videoicon,
.courses .course-cards .card .info .card-bottom-icons .downloadicon,
.courses .course-cards .card .info .card-bottom-icons .clockicon {
    color: gray;
}

.courses .course-cards .card .info .card-bottom-icons span {
    opacity: 0.7;
    font-size: 0.8rem;
    font-weight: 400;
    margin: 0 5px;
}

.courses .course-cards .card .join-course-btn {
    margin: 15px auto;
    padding: 5px 20px;
    background-color: #FF7426;
    border: none;
    border-radius: 30px;
    font-weight: 500;
    font-size: 0.9rem;
    color: white;
    cursor: pointer;
}

.courses .course-cards .card .join-course-btn:hover {
    box-shadow: 0px 0px 5px #000;
}

@media (max-width: 768px) {

    .courses .course-cards {
        display: flex;
        flex-direction: column;
        margin: 40px auto;
        position: relative;
    }
}

@media (max-width: 480px) {
    .courses .course-cards {
        display: flex;
        flex-direction: column;
        margin: 40px auto;
        position: relative;
    }
}














