/* for news letter */
.newsletter {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    background-color: #4d2c5e;
    height: 230px;
    width: 65vw;
    margin: 10px auto;
    border-radius: 10px;
    position: relative;
}

.newsletter .head {
    margin-bottom: 20px;
    color: white;
}

.newsletter .head h3 {
    font-size: 1.4rem;
    font-weight: 700;
}

.newsletter .head p {
    font-size: 0.8rem;
    font-weight: 300;
    opacity: 0.8;
}

.newsletter .input {
    width: 40%;
    display: flex;
    border-radius: 30px;
    box-shadow: 0px 0px 3px black;
    margin-bottom: 18px;
}

