@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
}


.thanks {
    height: 300px;
    width: 300px;
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 20px;
    padding: 50px;
    align-items: center;
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0px 0px 10px #4d2c5e;
    border: 5px solid #FF7426;
    position: relative;
}

.thanks .circleround-signin {
    position: absolute;
    top: 15px;
    left: 45px;
    height: 65px;
    width: 65px;
    object-fit: cover;
    animation: rotateCircleroundSignin 10s linear infinite;
}

@keyframes rotateCircleroundSignin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}






.thanks .thanksBox .logo-div {
    margin-top: -20px;
}

.thanks .thanksBox .logo-div img {
    width: 35px;
    height: 35px;
    object-fit: cover;
    transition: 0.4s;
    position: absolute;
    top: 30px;
    left: 60px;
}

.thanks .thanksBox .logo-div img:hover {
    transform: rotate(180deg);

}


.thanksBox .thanksText {
    margin-top: 50px;

}

.thanksBox .thanksText h1 {
    margin-bottom: 10px;
    font-weight: 700;
}

.thanksBox .thanksText p {
    font-weight: 400;
    margin-bottom: 20px;
}

.thanks .thanksBox .thanksButton .btn-link .thanksBtn {
    display: flex;
    align-items: center;
}

.thanks .thanksBox .thanksButton .thanksBtn {
    height: 40px;
    width: auto;
    margin-left: 50px;
    font-size: 0.9rem;
    padding: 6px 30px;
    background-color: #4d2c5e;
    color: white;
    border: none;
    border-radius: 30px;
    cursor: pointer;
}

.thanks .thanksBox .thanksButton .thanksBtn:hover {
    box-shadow: 0px 0px 5px black;
}

.thanks .thanksBox .thanksButton .thanksBtn:active {
    background: linear-gradient(to right, #FF7426, #4d2c5e);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}

@media (max-width: 768px),
(max-width: 480px) {

    .thanks {
        height: auto;
        width: 250px;
        display: flex;
        flex-direction: column;
        background-color: white;
        border-radius: 20px;
        padding-top: 50px;
        align-items: center;
        margin-top: 50px;
        margin-left: auto;
        margin-right: auto;
        box-shadow: 0px 0px 10px #4d2c5e;
        border: 5px solid #FF7426;
        position: relative;
    }
}