@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
}

.navbar {
    position: fixed;
    top: 0;
    z-index: 100;
    width: 100%;
    height: 70px;
    background-color: rgba(255, 255, 255, 0.812);
    display: flex;
    align-items: center;
}

.burger-menu {
    color: #4d2c5e;
    margin-right: 50px;
    font-size: 1.5rem;
    cursor: pointer;
    display: none;
}

.burger-menu:hover {
    color: #FF7426;
}

.mid ul {
    flex: 3;
    display: flex;
    align-items: center;
    margin-left: 200px;

}

.mid ul li .links {
    list-style: none;
    margin-right: 30px;
    font-size: 0.8rem;
    font-weight: bold;
    cursor: pointer;
    text-decoration: none;
    color: #4d2c5e;
}

.mid ul li .links:hover {
    color: #FF7426;

}


.menu {
    width: 100%;
    height: 100vh;
    background-color: #4d2c5e;
    position: absolute;
    top: 70px;
    z-index: -1;
    color: white;
}

.menu .circle-round-menu {
    position: absolute;
    height: 60px;
    width: 60px;
    object-fit: cover;
    top: 335px;
    left: 120px;
    animation: rotateMenu 10s linear infinite;
}

@keyframes rotateMenu {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.menu .circle-round-dotted-menu {
    position: absolute;
    height: 25px;
    width: 25px;
    object-fit: cover;
    top: 353px;
    left: 137px;
    animation: rotateMenu 10s linear infinite;
}

@keyframes rotateMenu {
    from {
        transform: rotate(360deg);
    }

    to {
        transform: rotate(0deg);
    }
}

.menu .circle-round-menu2 {
    position: absolute;
    height: 90px;
    width: 90px;
    object-fit: cover;
    top: 318px;
    left: 106px;
    animation: rotateMenu 10s linear infinite;
}

@keyframes rotateMenu {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}




.menu .menu-ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 20px auto;

}

.menu-ul li {
    margin: 20px;
    cursor: pointer;
}

.menu .menu-ul li .menu-links {
    text-decoration: none;
    color: white;
    display: flex;
    align-items: center;
    padding: 5px;
    text-align: left;
}

.menu .menu-ul li .menu-links:hover {
    color: #FF7426;
}

.menu .menu-ul button {
    height: 40px;
    width: 120px;
    font-size: 0.9rem;
    padding: 6px 30px;
    background-color: #FF7426;
    color: white;
    border: none;
    border-radius: 30px;
    cursor: pointer;
}

.menu .menu-ul button:hover {
    box-shadow: 0px 0px 5px black;
}







.left {
    flex: 1;
    display: flex;
    align-items: center;
    margin-left: 130px;
}

.left .logo {
    width: 35px;
    height: 35px;
    object-fit: cover;
    margin-right: 10px;
    cursor: pointer;
    transition: 0.4s;

}

.left .logo:hover {
    transform: rotate(180deg);
}

.left span .logo-span {
    font-weight: bold;
    font-size: 1rem;
    cursor: pointer;
    color: #4d2c5e;
    transition: 0.3;
    text-decoration: none;

}

.left span .logo-span:hover {
    color: #FF7426;
}






.right {
    flex: 1;
    position: relative;
}

.right .circleround {
    position: absolute;
    height: 40px;
    width: 40px;
    object-fit: cover;
    left: 60px;
    top: -18px;
}

.right .circleround {
    animation: rotateCircleRound 12s linear infinite;
}

@keyframes rotateCircleRound {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}


.right .smallgear {
    position: absolute;
    height: 20px;
    width: 20px;
    object-fit: cover;
    left: 70px;
    top: -8px;
}

.right .smallgear {
    animation: rotateSmallGear 16s linear infinite;
}

@keyframes rotateSmallGear {
    from {
        transform: rotate(360deg);
    }

    to {
        transform: rotate(0deg);
    }
}



.menu-ul li {
    list-style: none;
}








@media (max-width: 768px) {
    .navbar .mid {
        display: none;
    }

    .navbar .nav-menu .bars {
        display: block;
    }

    .left {
        margin-left: 20px;
    }

    .right .signIn {
        display: none;
    }

    .burger-menu {
        display: block;
    }
}

@media (max-width: 480px) {
    .navbar .mid {
        display: none;
    }

    .navbar .nav-menu .bars {
        display: block;
    }

    .left {
        margin-left: 20px;
    }

    .right .signIn {
        display: none;
    }

    .burger-menu {
        display: block;
    }
}