@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
}

.footer {
    display: flex;
    padding: 3rem 0;
    margin-top: 100px;
    background-color: #ff7426;
}

.footer .grid-4 {
    display: grid;
    grid-template-columns: 1;
}

.grid-4-col {
    padding: 0 1.7rem;
    color: #fff;
    font-size: 1rem;
}

.footer .title-sm {
    color: #4d2c5e;
    margin-bottom: 0.9rem;
    font-size: 1rem;
}


li {
    list-style: none;
}

.footer-links a {
    color: #fff;
    text-decoration: none;
    font-size: 0.9rem;
    display: inline-block;
    margin-bottom: 0.4rem;
    transition: 0.3s;
}

.footer-links a:hover {
    color: #4d2c5e
}




.bottom-footer {
    margin-top: 1.5rem;
    margin-bottom: 0.5rem;
    padding: 0 1.7rem;
    display: flex;
    align-items: center;
    justify-content: center
}

.copyright .text {
    color: #000;
    font-size: 1rem;
    display: inline-block;
    text-align: center;
}

.copyright .text span {
    color: #000;
    cursor: pointer;
}

.copyright .text span:hover {
    color: #ff7426;
}

.followme-wrap {
    display: flex;
}

.followme {
    display: flex;
    align-items: center;
}

.followme h3 {
    color: #4d2c5e;
    font-weight: 500;
    font-size: 1rem;
}

.followme .footer-line {
    width: 60px;
    height: 2px;
    background-color: #4d2c5e;
    margin: 0 1.2rem;
    display: inline-block;
}

.social-media a {
    color: #fff;
    font-size: 1rem;
    margin-right: 0.4rem;
    transition: 0.3s;
}

.social-media a:hover {
    color: #4d2c5e
}

.back-btn-wrap {
    position: relative;
    margin-left: 2.5rem;
}

.back-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 55px;
    width: 55px;
    background-color: #ff7426;
    border-radius: 50%;
    color: #fff;
    font-size: 1.2rem;
    box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.15);
    transition: 0.3s;
    position: relative;
    z-index: 2;
}

.back-btn:hover {
    background-color: #4d2c5e;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);

}

.back-btn-wrap:before {
    content: "";
    position: absolute;
    width: 60px;
    height: 60px;
    background-color: black;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0.7);
    border-radius: 50%;
    opacity: 0.08;
    animation: scale 1.2s ease-out infinite 1s;
}

@media (min-width: 1024px) {
    .footer .grid-4 {
        grid-template-columns: 3.5fr 2fr 2fr 2fr;
    }
}

@media (max-width: 768px) {
    .footer {

        margin-top: 100px;
        background-color: #ff7426;
    }

    .footer .grid-4 {
        grid-template-columns: 1fr 1fr;
    }

    .links,
    .support,
    .services {
        display: none;
    }
}

@media (max-width: 480px) {
    .footer {
        width: 100vw;
        margin-top: 100px;
        background-color: #ff7426;
    }

    .links,
    .support,
    .services {
        display: none;
    }
}