@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;

}

/* body {
    background-color: #4d2c5e;

} */

.contact-container {
    height: 75vh;
    width: 55vw;
    display: flex;
    align-items: center;
    margin-top: 120px;
    margin-right: auto;
    margin-left: auto;
    background-color: white;
    border: 5px solid #ff7426;
    border-radius: 50px;
    position: relative;
    justify-content: space-around;
    position: relative;

}

.contact-container .contactus {
    position: absolute;
    top: -34px;
    left: -24px;
    height: 40px;
    width: 40px;
    object-fit: cover;
}

.contact-container .circle-round-about {
    position: absolute;
    top: -54px;
    left: -44px;
    height: 80px;
    width: 80px;
    object-fit: cover;
    animation: rotateCircleRound 10s linear infinite;
}

@keyframes rotateCircleRound {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}


.contact-container:hover {
    box-shadow: 0px 0px 10px #ff7426;
}

.contact-container .contact-left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-content: space-around;
}

.contact-container .contact-left .left-heading {
    margin-bottom: 40px;
}

.contact-container .contact-left .left-heading h1 {
    line-height: 2rem;
}

.contact-container .contact-left .left-heading h1 span {
    color: #ff7426;
}

.contact-container .contact-left .left-mid {
    display: flex;
    flex-direction: column;
}

.contact-container .contact-left .left-mid .icon-detail {
    display: flex;
    align-items: center;
    margin: 8px 0
}

.contact-container .contact-left .left-mid .icon-detail .mid-icon {
    margin-right: 10px;

}

.contact-container .contact-left .left-bottom {
    margin-top: 60px;
}

.contact-container .contact-left .left-bottom .bottom-left-icon {
    margin-right: 20px;
    cursor: pointer;
}

.contact-container .contact-left .left-bottom .bottom-left-icon:hover {
    color: #ff7426;
    transition: 0.3s;

}


.contact-container .contact-right {
    display: flex;
    flex-direction: column;
    align-items: center;

}

.contact-container .contact-right .right-top-area {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-bottom: 40px;
}

.contact-container .contact-right .right-top-area .right-top-head {
    margin-bottom: 15px;
}

.contact-container .contact-right .right-top-area .right-top-buttons .row1 button,
.contact-container .contact-right .right-top-area .right-top-buttons .row2 button {
    margin-right: 10px;
    font-weight: 300;
    color: white;
    background-color: #4d2c5e;
    border: none;
    border-radius: 5px;
    padding: 5px 5px;
    cursor: pointer;
    font-size: 0.8rem;
}

.contact-container .contact-right .right-top-area .right-top-buttons .row1 {
    margin-bottom: 10px;

}

