.contact-container .contact-right .right-form-area form {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.contact-container .contact-right .right-form-area form input {
    margin-bottom: 15px;
    width: 255px;
    padding: 5px 10px;
    outline-color: #4d2c5e;
    border: none;
    box-shadow: 0px 0px 2px black;
    border-radius: 10px;
}

.contact-container .contact-right .right-form-area form textarea {
    margin-bottom: 15px;
    width: 255px;
    padding: 5px 10px;
    outline-color: #4d2c5e;
    border: none;
    box-shadow: 0px 0px 2px black;
    border-radius: 10px;
    resize: none;
}

.contact-container .contact-right .right-form-area form button {
    display: flex;
    align-items: center;
    width: 160px;
    font-weight: 600;
    color: white;
    background-color: #4d2c5e;
    border: none;
    border-radius: 10px;
    padding: 10px 15px;
    cursor: pointer;
    margin-bottom: 3px;
    position: relative;
    margin-top: 10px;
}

.contact-container .contact-right .right-form-area form button:hover {
    color: #ff7426;
    transition: 0.2s;
}

.contact-container .contact-right .right-form-area form button .icon {
    margin-left: 7px;
    size: 0.5rem;
    transform: rotate(-30deg);
    position: absolute;
    right: 10px;
    bottom: 12px;
}


@media (max-width: 768px),
(max-width: 480px) {

    .contact-container {
        height: auto;
        width: 95vw;
        padding: 30px 0px;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 120px;
        margin-right: auto;
        margin-left: auto;
        background-color: white;
        border: 5px solid #ff7426;
        border-radius: 50px;
        position: relative;
        justify-content: space-around;

    }

    .contact-container .contact-left {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 40px;
    }

    .contact-container .contact-left .left-heading {
        margin-bottom: 80px;
    }



}